.courses__wrap {
    margin-top: 6rem;
    position: relative;
    z-index: 3;
    width: 100%;
    height: auto;
}

.courses__head {
    width: 100%;
    height: auto;
    &-wrap {
        width: 100%;
        height: auto;
        background-image: $section-bg-grad;
        padding: 3rem 1.5rem 4rem 1.5rem;
    }
}



.fblock {
    &_title {
        color: $white-1;
        font-family: $regular;
        font-size: 1.2rem;
        font-weight: 400;
        line-height: 2.6rem;
        text-transform: uppercase;
        letter-spacing: 0.33px;
    }
    &_lead {
        color: $white-1;
        font-family: $regular;
        font-size: 1.2rem;
        font-weight: 400;
        line-height: 2.6rem;
        text-transform: uppercase;
        letter-spacing: 0.33px;
        padding: 4rem 0 4rem 0;
    }
    &::after {
        content: " ";
        width: 100%;
        height: 1px;
        background-color: $white-1;
        display: block;
        opacity: 0.2;
        margin: 0 auto;
    }
}

.sblock {
    padding-top: 4rem;
    p {
        color: $white-1;
        font-family: $regular;
        font-size: 1.2rem;
        font-weight: 400;
        line-height: 2.8rem;
        letter-spacing: 0.33px;
    }
 
    &_date {
        li {
            color: $white-1;
            font-family: $regular;
            &::before {
                color: $white-1;
            }
        }
        &:last-child {
            padding-top: 3rem;
        }
    }
}

.courses__slider {
    width: 100%;

    height: 122vw;

}

.courses__img {
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.courses__container {
    width: 100%;
    height: auto;
}

.first__inner {
    padding: 4rem 1.5rem 4rem 1.5rem;
}

.first__day {
    width: 100%;
    height: auto;
    h2 {
        font-weight: 400;
        letter-spacing: 0.28px;
        letter-spacing: 0.2px;
    }
    &-item {
        p {
            font-weight: 400;
            letter-spacing: 0.28px;
            letter-spacing: 0.2px;
        }
    }
}

.item_low {
    padding-bottom: 0;
    font-weight: 600;
    color: $p-color-1;
    font-family: $light;
    font-size: 1rem;
    line-height: 2.2rem;
}

.second__inner {
    width: 100%;
    height: auto;
    background: rgba(79, 111, 158, 0.1);
}

.second__day {
    padding: 4rem 1.5rem 4rem 1.5rem;
    h2 {
        font-weight: 400;
        letter-spacing: 0.28px;
        letter-spacing: 0.2px;
    }
}

.courses__btn-item {
    p {
        font-weight: 400;
        letter-spacing: 0.28px;
        letter-spacing: 0.2px;
    }
}

.courses__btn {
    top: -4.6rem;
    position: relative;
    &-bg.request-button__dark {
        background-color: #fff;
    }
}

.btn_about {
    background: #fff;
}

@media screen and (min-width: 768px) {
    .courses__wrap {
        margin-top: 13rem;
    }
    .courses__head {
        &-wrap {
            padding: 5rem 2rem 2rem 2rem;
        }
    }

    .fblock {
        &_title {
            font-size: 1.4rem;
            font-weight: 400;
            line-height: 2.4rem;
        }
        &_lead {}
    }
    .sblock {
        display: flex;
        &_number {
            width: 50%;
            height: auto;
            p {}
        }
        &_date {
            width: 50%;
            height: auto;
            li {}
            &:last-child {
                padding-top: 0;
            }
        }
    }
    .courses__slider {}
    .courses__img {}
    .courses__container {}
    .first__inner {
        padding: 4rem 1.5rem 4rem 1.5rem;
    }
    .first__day {
        -moz-column-count: 2;
        -moz-column-width: 200px;
        -moz-column-gap: 2em;

        -webkit-column-count: 2;
        -webkit-column-width: 200px;
        -webkit-column-gap: 2em;

        column-count: 2;
        column-width: 200px;
        column-gap: 2em;
        h2 {}
        &-item {
            p {}
        }
    }
    .item_low {}
    .second__inner {}
    .second__day {
        padding: 4rem 1.5rem 4rem 1.5rem;
        h2 {}
    }
    .courses__btn-item {
        p {}
    }
    .courses__btn {
        top: -6rem;
    }
}

@media screen and (min-width: 1200px) {
    .courses__btn {
        top: -8rem;
        background: none;
    }
    .btn_about {
        top: -6rem;
    }
}